/* eslint-disable */
import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from "react-router-dom";
import { Nav, DropdownButton, Dropdown } from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';

import './Detail.scss';
import { connect } from "react-redux";

/*let 박스 = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 };
`;*/

function Detail(props) {
    let history = useHistory();
    let { id } = useParams();
    let findGoods = props.shoes.find(function (goods) {
        goods.id == id ? console.log(id) : null;
        return goods.id == id;
    });

    let [alert, alertChange] = useState(true);
    let [tab, tabChange] = useState(0);
    let [sw, swChange] = useState(false);
    let [dd, ddChange] = useState('220');

    /* component 생성 후 timeOut 함수 실행 */
    /* 2초 지나면 alert를 false로 변환 */
    useEffect( () => {
        let timer = setTimeout( () => { alertChange(false), console.log("변경됨") }, 2000 );
        return () => { clearTimeout( timer ) }
    }, [ alert ] );

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <img src= { "https://codingapple1.github.io/shop/shoes" + (findGoods.id + 1) + ".jpg" } width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5"> { findGoods.title } </h4>
                    {
                        alert === true
                            ? (<div className="my-alert2">
                                <p>재고가 얼마 남지 않았습니다</p>
                               </div>)
                            : null
                    }
                    <p> { findGoods.content } </p>
                    <p> { findGoods.price } 원 </p>
                    <div style={{ display : "flex", justifyContent : "center" }}>
                        <DropdownButton id="dropdown-basic-button" title = "사이즈 변경" style={{ margin : "10px" }}>
                            <Dropdown.Item onClick = { () => { ddChange(220) } }>220</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(230) } }>230</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(240) } }>240</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(250) } }>250</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(260) } }>260</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(270) } }>270</Dropdown.Item>
                            <Dropdown.Item onClick = { () => { ddChange(280) } }>280</Dropdown.Item>
                        </DropdownButton>
                        <button className="btn btn-default" disabled="disabled"
                                style={{ fontSize : "24px", color : "black", fontWeight : "bold"}}> { dd } </button>
                    </div>
                    <button
                        className="btn btn-danger" style={ { margin: "10px" } }
                        onClick = { () => { props.dispatch({ type : '항목추가', payload : { name : findGoods.title , quan : 1 }});
                        history.push('/cart');
                        }}>주문하기
                    </button>
                    <button onClick = { () => {history.goBack()} } className = "btn btn-danger" style={ { margin: "10px" } }> 뒤로가기 </button>
                </div>
            </div>
            <div>
                <Info 재고 = { props.재고 }></Info>
            </div>
            <div>
                <Nav variant="tabs" defaultActiveKey="link-0">
                    <Nav.Item>
                        <Nav.Link eventKey="link-0" onClick={()=>{ tabChange(0) }}>상세정보</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="link-1" onClick={()=>{ tabChange(1) }}>주문</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="link-2" onClick={()=>{ tabChange(2) }}>문의</Nav.Link>
                    </Nav.Item>
                </Nav>

            </div>
            <TabContext tab = { tab } tabChange = { tabChange } />

        </div>
    )
}

function Info(props) {
    return (
        <p> 재고 : { props.재고[0] } </p>
    )
}

function TabContext (props) {
    switch (props.tab) {
        case 0 :
            return <div> { '상세정보' } </div>;
        case 1 :
            return <div> { '주문' } </div>;
        case 2 :
            return <div> { '문의' } </div>;
    }
}

function state를props화(state){
    return {
        state : state.reducer,
        alert열렸니 : state.reducer2
    }
}

export default connect(state를props화)(Detail)